.f-finder {
  margin-left: -20px;
  margin-bottom: 39px; }
  .f-finder .f-finder-inner {
    padding: 6px 20px 26px;
    background-color: #cc2529;
    float: left; }
  .f-finder h3 {
    text-transform: uppercase;
    line-height: 40px;
    margin: 0;
    font-size: 16px;
    color: white;
    font-weight: bold; }
  .f-finder input {
    line-height: 36px;
    padding: 0 10px;
    margin-right: 16px;
    width: 230px;
    border: none; }
  .f-finder button {
    display: inline-block;
    padding: 0 10px;
    height: 36px;
    line-height: 36px;
    background-color: #3761a7;
    border: none;
    color: white;
    font-weight: bold;
    text-transform: uppercase; }



