@font-face {
    font-family: 'OpenSans';
    src: url('./resources/OpenSans-Regular.svg');
    src: url('./resources/OpenSans-Regular.eot?#iefix') format('embedded-opentype'),
    url('./resources/OpenSans-Regular.woff') format('woff'),
    url('./resources/OpenSans-Regular.ttf') format('truetype'),
    url('./resources/OpenSans-Regular.svg#OpenSans-Regular') format('svg');
    font-weight: normal;
    font-style: normal;
}

