.part-group-lits {
  width: 235px; }

.part-group {
  margin-bottom: 2px; }
  .part-group .part-group-header {
    position: relative;
    padding: 8px 40px 8px 28px;
    background-color: #eeeeee;
    line-height: 17px;
    color: #3761a7;
    cursor: pointer; }
    .part-group .part-group-header .header-selectable {
      position: absolute;
      left: 0;
      top: 0;
      width: 28px;
      height: 33px;
      background: url("./resources/checkbox.png") center center no-repeat transparent; }
      .part-group .part-group-header .header-selectable.selected {
        background-image: url("./resources/checkbox-checked.png"); }
    .part-group .part-group-header .header-dropable {
      position: absolute;
      right: 0;
      top: 0;
      width: 33px;
      height: 33px;
      background: url("./resources/gray-arrow-down.png") 0 center no-repeat transparent; }
      .part-group .part-group-header .header-dropable.droped {
        background: url("./resources/gray-arrow-right.png") 5px center no-repeat; }
  .part-group .part-group-list {
    margin: 0;
    padding: 6px 0;
    list-style: none;
    color: #666666;
    font-size: 12px; }
    .part-group .part-group-list .part-group-list-item {
      margin: 0;
      padding: 4px 0 4px 45px;
      cursor: pointer;
      line-height: 16px;
      background: url("./resources/checkbox.png") 25px 5px no-repeat transparent; }
      .part-group .part-group-list .part-group-list-item.selected, .part-group .part-group-list .part-group-list-item:hover {
        color: #989898; }
      .part-group .part-group-list .part-group-list-item.selected {
        background-image: url("./resources/checkbox-checked.png"); }



